body {
  background: #f7f9fb;
  padding: 0 0 94px;
  max-width: 500px;
  margin: 0 auto;
}

.container {
  padding: 0 15px;
}

.nav {
  padding: 6px 0;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 52px;
}
.nav__logo img {
  width: 100%;
}
.nav__alert {
  position: relative;
  margin: 0 18px 0 auto;
}
.nav__alert span {
  font: 600 12px/1 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  position: absolute;
  top: -10px;
  right: -10px;
  background: #fa8787;
  border-radius: 100%;
}
.nav__blue {
  border: none;
  width: 162px;
  padding: 10px;
  font: 600 14px/1.71429 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  background: url("../img/btn_blue.svg") center no-repeat;
  background-size: contain;
}

.header {
  padding: 20px 0;
}
.header h1 {
  font: 700 56px/0.9 "Inter", sans-serif;
  letter-spacing: 0em;
  text-align: center;
  color: #000;
}
.header h1 span {
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__text {
  margin: 20px 0 0;
  font: 400 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.header__btn {
  margin: 20px 0 0;
  box-shadow: 0 4px 13px 0 rgba(110, 131, 253, 0.74);
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  width: 100%;
  padding: 24px 10px;
  font: 600 24px/1 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
  border: none;
  border-radius: 10px;
}
.header__price {
  margin: 20px 0 0;
  border-radius: 10px;
  background: #fff;
  padding: 15px;
  display: grid;
  gap: 10px;
}
.header__price_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__price_name {
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
}
.header__price_prof {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.header__price_prof p {
  font: 400 14px/1 "Inter", sans-serif;
  color: #707579;
}
.header__price_val {
  font: 600 36px/1 "Inter", sans-serif;
  letter-spacing: 0em;
  text-align: center;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__price_arrow {
  border-radius: 10px;
  padding: 10px 12px;
  background: rgba(0, 255, 178, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.header__price_arrow p {
  font: 400 16px/0.875 "Inter", sans-serif;
  color: #02d495;
}
.header__stat {
  margin: 20px 0 0;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.header__stat img {
  width: 100%;
  pointer-events: none;
}

.why {
  padding: 20px 0;
  background: #fff;
}
.why h2 {
  font: 700 24px/1.3 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.why h2 span {
  font: 700 24px/1.3 "Inter", sans-serif;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.why__wrapper {
  margin: 15px 0 0;
  display: grid;
  gap: 15px;
}
.why__item {
  padding: 15px 15px 128px;
  border-radius: 10px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}
.why__item p {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.why__item p + p {
  margin: 10px 0 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #fff;
}
.why__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom center;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.quest {
  padding: 40px 0 20px;
}
.quest h2 {
  font: 700 24px/1.29167 "Inter", sans-serif;
  letter-spacing: 0.01em;
  text-align: center;
  color: #000;
}
.quest h2 span {
  font: 700 24px/1.29167 "Inter", sans-serif;
  background: linear-gradient(90deg, #0098ea 0%, #a168ff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.quest__wrapper {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 6px;
}
.quest__num {
  font: 600 36px/1 "Inter", sans-serif;
  letter-spacing: 0em;
  color: #d9d9d9;
}
.quest__row {
  margin: 15px 0 0;
  display: grid;
  grid-template-columns: 1fr 32px;
  gap: 20px;
  align-items: start;
}
.quest__row p {
  font: 600 20px/1.2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #000;
}
.quest__arrow {
  width: 32px;
  height: 32px;
  border-radius: 100%;
  background: #efeff4;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quest__arrow path {
  transition: all 0.3s;
}
.quest__text {
  margin: 0;
  font: 400 16px/1.5 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
  transition: all 0.3s;
  max-height: 0;
  overflow: hidden;
}
.quest__item {
  border: 1px solid #efeff4;
  border-radius: 10px;
  padding: 15px;
  background: #fff;
}
.quest__item.active .quest__arrow {
  background: #010101;
  transform: rotate(45deg);
}
.quest__item.active .quest__arrow path {
  fill: white;
}
.quest__item.active .quest__text {
  max-height: 500px;
}

.nav-fix {
  padding: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  background: rgba(244, 244, 244, 0.75);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  z-index: 20;
}
.nav-fix p {
  font: 500 12px/2 "Inter", sans-serif;
  letter-spacing: 0.01em;
  color: #010101;
  text-align: center;
  transition: all 0.3s;
}
.nav-fix svg {
  margin: 0 auto;
  display: block;
}
.nav-fix svg path {
  fill: #010101;
  transition: all 0.3s;
}
.nav-fix .active p {
  color: #0098ea;
}
.nav-fix .active path {
  fill: #0098ea;
}